.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  position: fixed;
  width: 100%;
  z-index: 1;

  @media (max-width: 1023px) {
    position: static;
    z-index: 1;
  }
}

.header__container {
  display: flex;
  align-items: center;
  padding: 30px 20px 35px 20px;
  max-width: 1265px;
  margin: 0 auto;

  @media (max-width: 1023px) {
    padding: 20px 50px 25px 50px;
    max-width: 1023px;
    align-items: flex-start;
  }

  @media (max-width: 767px) {
    padding: 13px 35px 20px 26px;
    max-width: 1023px;
  }
}

.header__logo {
  margin-right: auto;
  outline: none;
  cursor: default;

  @media (max-width: 1023px) {
    margin: 0;
  }

  &:active:not(:focus) {
    outline: none;
  }

  &:focus {
    outline: 1px solid #358cbe;
  }
}

.header__user-nav {
  margin-right: 147px;

  @media (max-width: 1023px) {
    margin: 0;
  }
}

.header__button {
  display: block;
  border: none;
  box-sizing: border-box;
  cursor: pointer;
  outline: 1px solid #f2f5f7;
  border-radius: 3px;
  padding: 10px 15px;

  &--order-call {
    min-width: 160px;
    max-width: 160px;
    margin-right: 62px;
    padding: 10px 15px;
    background: transparent;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #f2f5f7;
  }

  @media (max-width: 1023px) {
    display: none;
  }

  &:hover {
    background: #008bc7;
    outline: none;
  }

  &:active {
    background: #006590;
    outline: 1px solid #a9d3eb;
  }

  &:focus {
    outline: 1px solid #3972c1;
  }
}
