.products {
  background: #041827;
  padding: 66px 0 140px 0;

  @media (max-width: 1023px) {
    padding: 55px 47px 75px 50px;
  }

  @media (max-width: 767px) {
    padding: 50px 25px 86px 25px;
  }
}

.products__container {
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;

  @media (max-width: 1023px) {
    padding: 0;
  }
}

.products__title {
  font-weight: 400;
  margin: 0 0 54px 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 36px;
  line-height: 51px;
  color: #ffffff;

  @media (max-width: 1023px) {
    margin: 0 0 37px 0;
  }

  @media (max-width: 767px) {
    display: none;
  }
}

.products__title-mobile {
  display: none;

  @media (max-width: 767px) {
    width: 97%;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    margin: 0 0 26px 0;
    font-size: 28px;
    line-height: 140.62%;
    color: #ffffff;
    display: block;
  }
}

.products__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  justify-content: space-between;
  grid-template-columns: repeat(3, minmax(250px, 350px));
  gap: 85px;

  @media (max-width: 1023px) {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.products__item {
  background: url("../img/background/products-image.jpg") no-repeat;
  background-size: cover;

  &--production {
    background-position: 16.5% 69%;
  }

  &--montage {
    background-position: 46.7% 72%;
  }

  &--accessories {
    background-position: 78.1% 65.3%;
  }

  @media (max-width: 1023px) {
    background-size: auto;

    &--production {
      background-position: -249px -549px;
    }

    &--montage {
      background-position: 76% 55%;
    }

    &--accessories {
      background-position: 18% 63%;
    }
  }

  @media (max-width: 767px) {
    &--production {
      background-position: 30.6% 52.7%;
    }

    &--montage {
      background-position: 29.7% 94.5%;
    }

    &--accessories {
      background-position: 72.3% 69.6%;
    }
  }

  @media (min-resolution: 2dppx) {
    background-image: url("../img/background/products-image@2x.jpg");
  }

  &:hover {
    transform: scale(0.9);
  }
}

.products__item a {
  outline: none;
  position: relative;
  display: block;
  min-height: 650px;
  padding: 47px 0 60px 40px;
  background-color: rgba(4, 24, 39, 0.5);

  @media (max-width: 1023px) {
    min-height: 250px;
    padding: 38px 0 60px 45px;
  }

  @media (max-width: 767px) {
    padding: 26px 0 60px 30px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 60px;
    height: 60px;
    bottom: 0;
    right: 0;
    background: #00b2ff;
    border-radius: 3px 0 0 0;

    @media (max-width: 767px) {
      width: 40px;
      height: 40px;
    }
  }

  &::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    bottom: 23px;
    right: 19px;
    background: url("../img/svg/arrow.svg") no-repeat;
    border-radius: 3px 0 0 0;

    @media (max-width: 767px) {
      bottom: 14px;
      right: 11px;
    }
  }

  &:focus {
    outline: 1px solid #358cbe;
  }
}

.products__item h3 {
  width: 165px;
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 20px;
  line-height: 30px;
  color: #ffffff;

  @media (max-width: 1023px) {
    width: 150px;
  }

  @media (max-width: 767px) {
    font-size: 18px;
    line-height: 30px;
    width: 135px;
  }
}
