.user-nav {
  width: 480px;
  margin-right: auto;
  flex-wrap: wrap;

  @media (max-width: 1023px) {
    width: 90%;
    justify-content: flex-end;
  }
}

.user-nav__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 2px 0 6px 0;

  @media (max-width: 1023px) {
    padding: 0 0 6px 0;
    justify-content: inherit;
  }

  @media (max-width: 767px) {
    padding: 0;
  }
}

.user-nav__list li {
  display: flex;
  width: 225px;

  @media (max-width: 1023px) {
    width: 40px;
    height: 40px;
    padding: 0 0 6px 0;
  }

  @media (max-width: 767px) {
    flex-wrap: wrap;
    width: 40px;
    height: 40px;
  }
}

.user-nav__list li:not(:last-child) {
  @media (max-width: 767px) {
    margin-right: 20px;
  }
}

.user-nav a {
  line-height: 18px;
  width: 225px;
  display: flex;
  padding: 12px 0 12px 0;
  outline: none;

  @media (max-width: 1023px) {
    width: 40px;
    height: 40px;
    padding: 10px;
  }

  @media (max-width: 767px) {
    padding: 7px 9px;
  }

  &:active {
    background: #1d76e399;
  }

  &:focus {
    outline: 1px solid #358cbe;
  }
}

.user-nav__link-icon {
  margin-right: 15px;

  @media (max-width: 1023px) {
    margin-right: 0;
  }
}

.user-nav span {
  line-height: 18px;
  color: #f2f5f7;

  @media (max-width: 1023px) {
    display: none;
  }

  &:hover {
    color: #358cbe;
  }
}
